
export class ColorUtil {

  public static hexToRgb(hex: string): { r: number, g: number, b: number } {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16)
    } : null;
  }

  public static rgbToHex(r: number, g: number, b: number): string {
    return [r, g, b].map((x) => {
      const hex = x.toString(16);
      return hex.length === 1 ? '0' + hex : hex;
    }).join('');
  }

  public static getTextColor(color: string): string {
    const rgb = ColorUtil.hexToRgb(color);
    if (rgb) {
      const brightness = Math.round(((rgb.r * 299) + (rgb.g * 587) + (rgb.b * 114)) / 1000);
      return brightness > 125 ? 'black' : 'white';
    }
    return 'black';
  }

  public static shouldTextBeDark(color: string): boolean {
    const rgb = ColorUtil.hexToRgb(color);
    if (rgb) {
      const brightness = Math.round(((rgb.r * 299) + (rgb.g * 587) + (rgb.b * 114)) / 1000);
      return brightness > 125;
    }
    return true;
  }

  public static changeColorOpacity(color: string, opacity: number): string {
    if (opacity > 1) {
      opacity /= 100;
    }
    const rgb = ColorUtil.hexToRgb(color);
    if (rgb) {
      return `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${opacity})`;
    }
    return color;
  }

  public static changeColorBrightnessPercent(color: string, percent: number): string {
    const rgb = ColorUtil.hexToRgb(color);
    if (rgb) {
      return '#' + ColorUtil.rgbToHex(
        Math.min(255, Math.max(0, Math.round(rgb.r * (1 + percent / 100)))),
        Math.min(255, Math.max(0, Math.round(rgb.g * (1 + percent / 100)))),
        Math.min(255, Math.max(0, Math.round(rgb.b * (1 + percent / 100))))
      );
    }
    return color;
  }
}
