import { ConfigUtil } from './../../../../common/util/config.util';
import { ObservableValue } from 'mrd-core';
import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, booleanAttribute } from '@angular/core';
import { ColorUtil } from '../../../../common/util/color.util';
import { SConfigModel } from '../../../../common/model/config.model';

@Component({
  selector: 'mrd-button',
  templateUrl: './mrd-button.component.html',
  styleUrls: ['./mrd-button.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MrdButtonComponent implements OnInit, AfterViewInit {

  @Input({alias: 'icon-button', transform: booleanAttribute}) icon: boolean = false;
  @Input({alias: 'raised-button', transform: booleanAttribute}) raised: boolean = false;
  @Input({alias: 'outline-button', transform: booleanAttribute}) outline: boolean = false;
  @Input({alias: 'flat-button', transform: booleanAttribute}) flat: boolean = false;
  @Input({alias: 'fab-button', transform: booleanAttribute}) fab: boolean = false;
  @Input({alias: 'miniFab-button', transform: booleanAttribute}) miniFab: boolean = false;

  @Input({transform: booleanAttribute}) public set primary(value: boolean) {
    this._primary = value;
    if (value) {
      this.bgColor = this._config.baseColors.primary;
      this.textColor = ColorUtil.shouldTextBeDark(this._config.baseColors.primary) ? this.textDarkColor : this.textLightColor;
      this.hoverColor = ColorUtil.changeColorOpacity(this._config.baseColors.primary, 10);
      this.activeColor = ColorUtil.changeColorOpacity(ColorUtil.changeColorBrightnessPercent(this._config.baseColors.primary, 10), 30);
    }
  }
  public get primary(): boolean {
    return this._primary;
  }
  private _primary: boolean = false;
  @Input({transform: booleanAttribute}) public set accent(value: boolean) {
    this._accent = value;
    if (value) {
      this.bgColor = this._config.baseColors.accent;
      this.textColor = ColorUtil.shouldTextBeDark(this._config.baseColors.accent) ? this.textDarkColor : this.textLightColor;
      this.hoverColor = ColorUtil.changeColorOpacity(this._config.baseColors.accent, 20);
      this.activeColor = ColorUtil.changeColorOpacity(ColorUtil.changeColorBrightnessPercent(this._config.baseColors.accent, 10), 30);
    }
  }
  public get accent(): boolean {
    return this._accent;
  }
  private _accent: boolean = false;
  @Input({transform: booleanAttribute}) public set warn(value: boolean) {
    this._warn = value;
    if (value) {
      this.bgColor = this._config.baseColors.warn;
      this.textColor = ColorUtil.shouldTextBeDark(this._config.baseColors.warn) ? this.textDarkColor : this.textLightColor;
      this.hoverColor = ColorUtil.changeColorOpacity(this._config.baseColors.warn, 20);
      this.activeColor = ColorUtil.changeColorOpacity(ColorUtil.changeColorBrightnessPercent(this._config.baseColors.warn, 10), 30);
    }
  }
  public get warn(): boolean {
    return this._warn;
  }
  private _warn: boolean = false;

  @Input({transform: booleanAttribute}) public set disabled(value: boolean) {
    this._disabled = value;
  };
  public get disabled(): boolean {
    return this._disabled;
  }
  private _disabled: boolean = false;
  @Input() public loading: ObservableValue<boolean> = new ObservableValue(false);

  // @Output() public click: EventEmitter<void> = new EventEmitter();

  private _config: SConfigModel = ConfigUtil.getConfig();

  private _defaultBgColor: string = 'transparent';
  private _defaultTextLightColor: string = '#ffffff';
  private _defaultTextDarkColor: string = '#000000';
  private _defaultHoverColor: string = '#d3d3d361';
  private _defaultActiveColor: string = '#d3d3d3';
  private _defaultDisabledTextColor: string = '#a6a6a6';
  private _defaultDisabledBgColor: string = '#d3d3d3';

  private _defaultOutlineBorderWidth: string = '1px';
  private _defaultOutlineBorderStyle: string = 'solid';
  private _defaultOutlineBorderColor: string = '#d3d3d3';

  public bgColor: string = this._config?.button?.defaultBg || this._defaultBgColor;
  private textLightColor: string = this._config?.button?.textLight || this._defaultTextLightColor;
  private textDarkColor: string = this._config?.button?.textDark || this._defaultTextDarkColor;
  public textColor: string = this.textDarkColor;
  public hoverColor: string = this._config?.button?.defaultHover || this._defaultHoverColor;
  public activeColor: string = this._config?.button?.defaultActive || this._defaultActiveColor;
  public disabledTextColor: string = this._config?.button?.defaultDisabledTextColor || this._defaultDisabledTextColor;
  public disabledBgColor: string = this._config?.button?.defaultDisabledBgColor || this._defaultDisabledBgColor;

  public outlineBorderWidth: string = this._config?.button?.outline?.borderWidth || this._defaultOutlineBorderWidth;
  public outlineBorderStyle: string = this._config?.button?.outline?.broderStyle || this._defaultOutlineBorderStyle;
  public outlineBorderColor: string = this._config?.button?.outline?.border || this._defaultOutlineBorderColor;

  constructor(
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    let themesCount = [this.primary, this.accent, this.warn].filter((value) => value).length;
    if (themesCount > 1) {
      console.error('Only one theme can be set at a time. Please set only one of the following: primary, accent, warn');
    }
    let appearanceCount = [this.icon, this.raised, this.outline, this.flat, this.fab, this.miniFab].filter((value) => value).length;
    if (appearanceCount > 1) {
      console.error('Only one appearance can be set at a time. Please set only one of the following: icon, raised, outline, flat, fab, miniFab');
    }
    if (appearanceCount === 0 || this.outline || this.icon) {
      if (themesCount === 1) {
        this.textColor = this.bgColor;
        this.bgColor = this._defaultBgColor;
      }
      this.disabledBgColor = 'transparent';
    }
    if ((this.raised || this.flat) && themesCount === 0) {
      this.bgColor = 'white';
    }

    this.cdr.detectChanges();
  }
}
